






import Vue from "vue";
import { Component, Prop } from "vue-property-decorator";
import Toast from "@/components/Toast.vue";

@Component({
  components: { Toast }
})
export default class ToastManager extends Vue {
  @Prop() toast!: any;

  duration = 4000;

  addToast() {
    setTimeout(() => {
      this.removeToast();
    }, this.duration);
  }

  removeToast() {
    this.toast.show = false;
    clearInterval();
  }

  hideToast() {
    this.removeToast();
  }

  mounted() {
    setInterval(() => {
      this.addToast();
    }, this.duration);
  }
}
